<template>
  <div class="header" >
    <div class="content-wrapper">
        <div class="avatar">
            <img width="64" height="64" :src="seller.avatar">
        </div>
        <div class="content">
          <div class="title">
            <span class="brand"></span>
            <span class="name">{{seller.name}}</span>
          </div>
          <div class="description">
              {{seller.description}}/{{seller.deliveryTime}}分钟送达
          </div>
          <div v-if="seller.supports" class="support" >
              <span class="icon" :class="this.classMap[seller.supports[0].type]"></span>
              <span class="text"> {{seller.supports[0].description}}</span>
          </div>
          <div class="support-count" v-if="seller.supports" @click="DetailShowFn">
              <span class="count">{{seller.supports.length}}个</span>
              <i class="icon-keyboard_arrow_right"></i>
          </div>
        </div>
    </div>
    <div class="bulletin-wrapper">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <i class="icon-keyboard_arrow_right right2"></i>
    </div>
    <div class="background">
      <img width="100%" height="100%"  :src="seller.avatar">
    </div>
    <transition name="fade">
      <div class="detail" v-show="DetailShow">
        <div class="detail-wrapper">
            <div class="detail-main clearfix">
                <h1 class="name">{{seller.name}}</h1>
                <div class="star-wrapper">
                  <star :score="seller.score" :size="48"></star>
                </div>
                <div class="title">
                    <div class="line"></div>
                    <div class="text">优惠信息</div>
                    <div class="line"></div>
                </div>
                <ul v-if="seller.supports" class="supports">
                  <li class="support-item" v-for="(item, index) in seller.supports">
                    <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                    <span class="text">
                      {{seller.supports[index].description}}
                    </span>
                  </li>
                </ul>
                <div class="title">
                  <div class="line"></div>
                  <div class="text">商家信息</div>
                  <div class="line"></div>
                </div>
                <div class="bulletin">
                    <p class="content">{{seller.bulletin}}</p>
                </div>
            </div>
        </div>
        <div class="detail-close" @click="DetailHideFn">
          <i class="icon-close"></i>
        </div>
    </div>
    </transition>
  </div>
</template>



<script type="text/ecmascript-6">
  import star from '../../components/star/star.vue';
  export default{
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        DetailShow: false
      };
    },
    methods: {
      DetailShowFn() {
        this.DetailShow = true;
      },
      DetailHideFn() {
        this.DetailShow = false;
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    components: {
      star
    }
  };

</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/style/mixin.styl"
  .header
      color: #fff
      position: relative
      background: rgba(7, 17, 27, 0.5)
      overflow: hidden
      .content-wrapper
          padding:24px 12px 18px 24px
          font-size: 0
          .avatar
              display:inline-block
              vertical-align: top
              img
                border-radius: 2px
          .content
              display:inline-block
              font-size: 14px
              margin-left: 16px
            .support-count
              position: absolute
              right: 8px
              bottom: 35px
              padding:0 8px
              height: 24px
              line-height: 24px
              border-radius: 14px
              background: rgba(0, 0, 0, 0.2)
              text-align: center
              .count
                font-size: 10px
                vertical-align: top
                display: inline-block
              .icon-keyboard_arrow_right
                  margin-left: 2px
                  font-size: 10px
                  line-height: 24px

            .title
                  margin: 2px 0 8px 0
                  .brand
                      display: inline-block
                      width: 30px
                      height: 18px
                      bg-image("brand")
                      background-size: 30px 18px
                      background-repeat: no-repeat
                      vertical-align: top
                      .name
                        font-size: 16px
                        line-height: 18px
                        font-weight: bold
                        margin-left: 6px
            .description
              margin-bottom: 10px
              font-size: 12px
              line-height: 12px
            .support
                .icon
                    display: inline-block
                    vertical-align: top
                    width: 12px
                    height: 12px
                    margin-right: 4px
                    background-size: 12px
                    background-repeat: no-repeat
                    &.decrease
                        bg-image('decrease_1')
                    &.discount
                      bg-image('discount_1')
                    &.guarantee
                      bg-image('guarantee_1')
                    &.invoice
                      bg-image('special_1')
                    &.special
                      bg-image('special_1')
                .text
                  line-height: 12px
                  font-size: 10px
                  vertical-align: top

      .bulletin-wrapper
          height: 28px
          line-height: 28px
          padding: 0 22px 0 12px
          overflow: hidden
          text-overflow: ellipsis
          white-space: nowrap
          position: relative
          background: rgba(7, 17, 27, 0.2)

        .bulletin-title
            display: inline-block
            width: 22px
            height: 12px
            bg-image("bulletin")
            background-size: 22px 12px
            background-repeat:  no-repeat
            vertical-align: top
            margin-top: 8px
        .bulletin-text
            font-size: 12px
            margin: 0 4px
            vertical-align: top
        .right2
            position: absolute
            font-size: 10px
            right: 12px
            top: 8px

      .background
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1
        filter: blur(10px)

      .detail
        position: fixed
        z-index: 500
        overflow: auto
        width: 100%
        height: 100%
        left: 0
        top: 0
        background: rgba(7, 17, 27, 0.8)
        opacity: 1
        -webkit-backdrop-filter: blur(10px)
        &.fade-enter-active, &.fade-leave {
          transition: all .5s
        }
        &.fade-enter, &.fade-leave-active {
          transition: all .5s
          background: rgba(7, 17, 27, 0)
          opacity: 0
        }
        .detail-wrapper
          min-height: 100%
          width: 100%
          .detail-main
            margin-top: 64px
            padding-bottom: 64px
            width: 100%
            .name
              line-height: 16px
              font-size: 16px
              font-weight: 700
              text-align: center
            .star-wrapper
              margin-top: 18px
              padding: 2px 0
              text-align: center
            .title
              display: flex
              width: 80%
              margin: 28px auto 24px auto
              .line
                flex: 1
                position: relative
                top: -6px
                border-bottom: 1px solid rgba(255,255,255,0.2)
              .text
                padding: 0 12px
                font-size: 14px
                font-weight: 700
            .supports
              width: 80%
              margin: 0 auto
              .support-item
                  padding: 0 12px
                  margin-bottom: 12px
                  font-size: 0
                  &:last-child
                    margin-bottom: 0
                  .icon
                    display: inline-block
                    width: 16px
                    height: 16px
                    vertical-align: top
                    margin-right: 6px
                    background-size: 16px 16px
                    background-repeat: no-repeat
                    &.decrease
                      bg-image('decrease_2')
                    &.discount
                      bg-image('discount_2')
                    &.guarantee
                      bg-image('guarantee_2')
                    &.invoice
                      bg-image('special_2')
                    &.special
                      bg-image('special_2')
                  .text
                    line-height: 16px
                    font-size: 12px
            .bulletin
              width: 80%
              margin: 0 auto
              .content
                padding: 0 12px
                line-height: 24px
                font-size: 12px
        .detail-close
          position: relative
          width: 32px
          height: 32px
          margin: -64px auto 0 auto
          font-size: 32px


</style>



